<template>
  <div>
    <van-card class="card-custom">
      <template #title>
        <p style="font-size: 14px; font-weight: bold"><van-tag mark type="primary" size="medium">巡更</van-tag> {{ item.name }}</p>
      </template>
      <template #price>
        <van-tag :type="statusChange(item.state)">{{ item.state | stateFilter }}</van-tag>
      </template>
      <template #desc>
        <p>从{{ item.plan_start }}开始</p>
      </template>
      <template #footer>
        <van-button v-if="item.state === 10" round type="primary" size="small" icon="eye-o" style="width: 80px" @click="toSign(item)">查看</van-button>
        <van-button v-if="item.plan_type ===1 && (item.state === 0 || item.state === 1)" round type="primary" size="small" icon="scan" style="width: 80px" @click="toSign(item)">签到</van-button>
        <van-button v-if="item.plan_type ===2 && (item.state === 0 || item.state === 1)" round type="primary" size="small" icon="edit" style="width: 80px" @click="toSign(item)">签字</van-button>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: 'QcCard',
  filters: {
    stateFilter(val) {
      const map = {
        0: '未开始',
        1: '进行中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  props: {
    item: {
      type: Object,
      require: true,
      default() { return {} }
    }
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    toSign(item) {
      this.$utils.routerUrl('/qc/step-list', { plan_type: item.plan_type, ticket_id: item.id, report_flag: item.report_flag, scan_flag: item.scan_flag, sign_mode: item.sign_mode })
    }
  }
}
</script>
