<template>
  <div>
    <van-card class="card-custom">
      <template #title>
        <p style="font-size: 14px; font-weight: bold"><van-tag mark type="primary" size="medium">任务</van-tag> {{ item.name }}</p>
      </template>
      <template #price>
        <van-tag :type="statusChange(item.state)">{{ item.state | statusFilter }}</van-tag>
      </template>
      <template #desc>
        <p>{{ item.note }}</p>
      </template>
      <template #footer>
        <van-button v-if="item.state === 10" size="small" round type="primary" icon="eye-o" style="width: 80px;" @click="detailTask(item)">查看</van-button>
        <van-button v-if="item.state !== 10 && item.state !== 20" size="small" round type="primary" icon="records-o" style="width: 80px;" @click="dispose(item)">处理</van-button>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: 'TaskCard',
  filters: {
    statusFilter(val) {
      const map = {
        0: '未开始',
        1: '进行中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  props: {
    item: {
      type: Object,
      require: true,
      default() { return {} }
    }
  },
  data() {
    return {}
  },
  mounted() {
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    detailTask(item) {
      this.$utils.routerUrl('/ticket/task-detail', { id: item.id, state: item.state })
      // this.$router.push({ path: '/ticket/task-detail', query: { id: item.id, state: item.state }})
    },
    dispose(item) {
      this.$utils.routerUrl('/ticket/task-dispose', { templateId: item.template_id, id: item.id })
      // this.$router.push({ path: '/ticket/task-dispose', query: { templateId: item.template_id, id: item.id }})
    }
  }
}
</script>

