<template>
  <div>
    <van-card class="card-custom">
      <template #title>
        <p style="font-size: 14px; font-weight: bold"><van-tag mark type="primary" size="medium">保洁</van-tag> {{ item.name }}</p>
      </template>
      <template #price>
        <van-tag :type="statusChange(item.state)">{{ item.state | stateFilter }}</van-tag>
      </template>
      <template #desc>
        <p>从{{ item.plan_start }}开始</p>
      </template>
      <template #footer>
        <van-button round type="primary" size="small" icon="eye-o" style="width: 80px" @click="toSign(item)">查看</van-button>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: 'CleanCard',
  filters: {
    stateFilter(val) {
      const map = {
        0: '未开始',
        1: '进行中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  props: {
    item: {
      type: Object,
      require: true,
      default() { return {} }
    }
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    async toSign(item) {
      this.$utils.routerUrl('/clean/ticket-info', { ticket_id: item.id })
    }
  }
}
</script>

<style lang="less" scoped>
.app-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f5f6fa;
  color: rgb(153, 153, 153);
  .card{
    background: #ffffff;
    border-radius: 10px;
    margin: 10px;
    padding: 8px 10px;
    p{
      margin: 0 0 5px 0;
    }
    .row{
      display: flex;
      justify-content: space-between;
      .title{
        font-size: 16px;font-weight: bold;
      }
      .info{
        font-size: 14px;color: #888;
      }
    }
  }
  /deep/ .van-card__content{
    min-height: 30px!important;
  }
}
</style>
