<template>
  <div>
    <van-tabs v-model="active" @click="activeClick">
      <van-tab v-for="(name, index) in tabList" :key="index" :title="name" :style="`position: fixed; background-color: #f7f8fa; height: 100%`">
        <van-pull-refresh v-model="refreshing" :style="`height: ${refreshHeight}px;overflow:auto;`" @refresh="onRefresh()">
          <van-empty v-if="list.length === 0" image="search" description="暂无数据" />
          <template v-else>
            <div v-for="(item, key) in list" :key="key" style="">
              <clean-card v-if="item.data_type === 'clean'" :item="item" />
              <qc-card v-if="item.data_type === 'qc'" :item="item" />
              <transport-card v-if="item.data_type === 'transport'" :item="item" />
              <task-card v-if="item.data_type === 'task'" :item="item" />
            </div>
          </template>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import QcCard from '@/views/ticket/qc/index.vue'
import TaskCard from '@/views/ticket/task/index.vue'
import CleanCard from '@/views/ticket/clean/index.vue'
import TransportCard from '@/views/ticket/transport/index.vue'

export default {
  name: 'TicketList',
  components: {TransportCard, CleanCard, TaskCard, QcCard},
  filters: {
    statusFilter(val) {
      const map = {
        0: '未开始',
        1: '进行中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  data() {
    return {
      active: 0,
      tabList: ['全部', '已完成', '已过期'],
      listAll: [],
      list: [],
      refreshing: false,
      refreshHeight: window.innerHeight - 54
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    async getList() {
      this.active = 0
      const res = await this.$post('/ticket/list', {})
      if (res && res.length > 0) {
        this.listAll = res.filter(it => {
          return it.state === 10 || it.state === 20
        })
      } else {
        this.listAll = []
      }
      this.activeClick(0)
    },
    activeClick(index, title) {
      // 初始化
      this.list = []
      // 全部
      if (index === 0) {
        this.list = this.listAll
      }
      // 已完成
      if (index === 1) {
        this.list = this.listAll.filter(item => {
          return item.state === 10
        })
      }
      // 已过期
      if (index === 2) {
        this.list = this.listAll.filter(item => {
          return item.state === 20
        })
      }
    },
    onRefresh() {
      setTimeout(() => {
        this.refreshing = false
        this.getList()
      }, 1000)
    }
  }
}
</script>

