<template>
  <div>
    <van-card class="card-custom">
      <template #title>
        <p style="font-size: 14px; font-weight: bold"><van-tag mark type="primary" size="medium">运送</van-tag> {{ item.item_name }}</p>
      </template>
      <template #price>
        <van-tag :type="statusChange(item.state)">{{ item.state | stateFilter }}</van-tag>
      </template>
      <template #desc>
        <p>运送路径：{{ item.name }}</p>
        <p>下单时间：{{ item.create_time }}</p>
        <p v-if="item.state === 2">取件时间：{{ item.get_time }}</p>
        <p v-if="item.state === 10">完成时间：{{ item.complete_time }}</p>
      </template>
      <template #footer>
        <van-button round type="primary" size="small" icon="eye-o" style="width: 100px" @click="toSign(item)">查看</van-button>
        <!--已派工-->
        <van-button v-if="item.state === 1" round type="primary" size="small" icon="add-o" style="width: 100px" @click="toPickUp(item)">取件</van-button>
        <!--运送中-->
        <van-button v-if="item.state === 2" round type="primary" size="small" icon="checked" style="width: 100px" @click="toComplete(item)">确认签收</van-button>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: 'TransportCard',
  filters: {
    stateFilter(val) {
      const map = {
        0: '未开始',
        1: '已派工',
        2: '运送中',
        10: '已完成',
        20: '已过期'
      }
      return map[val]
    }
  },
  props: {
    item: {
      type: Object,
      require: true,
      default() { return {} }
    }
  },
  methods: {
    statusChange(status) {
      const map = { 0: 'default', 1: 'success', 10: 'primary', 20: 'danger'}
      return map[status]
    },
    toSign(item) {
      this.$utils.routerUrl('/transport/info', { ticket_id: item.id })
    },
    async toPickUp(item) {
      const confirm = await this.$dialog.confirm({
        title: '标题',
        message: '是否确认取件？'
      }).catch(() => {})
      if (!confirm) return

      await this.$http.post('/transport-ticket/pick-up', { ticket_id: item.id })
      await this.$toast.success('操作成功！')
      this.$emit('refreshList')// 刷新
    },
    toComplete(item) {
      this.$utils.routerUrl('/transport/sign', { ticket_id: item.id })
    }
  }
}
</script>
